import React, { lazy } from "react";
import { useParams } from "react-router-dom";
import { Endpoint, Part } from "types";
import Page, {
  LeftPanel,
  RightPanel,
  PageContentBase,
} from "components/pages/Page";
import { Space } from "antd";
import { CodeEditor, FullEditor } from "components/ui/Editor/Editor";
import { useQueriesTyped } from "components/utils/queryClient";
import { useQuery } from "react-query3";
import axios from "axios";
const UrlCard = lazy(() => import("./components/url-card"));
const VariablesCard = lazy(() => import("./components/variables-card"));
const PowerShellCommandCard = lazy(() => import("./components/command-card"));
const PowerShellOutputCard = lazy(() => import("./components/output-card"));

export default function Api() {
  const { id } = useParams();

  const { data: endpoint } = useQuery<Endpoint>(
    `/endpoint/${id}`,
    { enabled: false }
  );

  const [{ data: parts }] = useQueriesTyped([
    {
      queryKey: `/endpoint/${id}/parts`,
      queryFn: async () =>
        (await axios.get<Part[]>(`/api/v1/endpoint/${id}/parts`)).data,
      enabled: !!id,
      refetchOnWindowFocus: false,
    },
  ]);

  return (
    <Page
      resource={{
        ...endpoint,
        parts,
        fullUrl: `${window.location.origin}${endpoint?.url}`,
      }}
    >
      <PageContentBase>
        <LeftPanel>
          <CodeEditor>
            <FullEditor title="Editor" height="calc(100vh - 200px)" />
          </CodeEditor>
        </LeftPanel>
        <RightPanel>
          <Space direction="vertical" size="middle" style={{ width: "100%" }}>
            <UrlCard />
            <VariablesCard />
            <PowerShellCommandCard />
            <PowerShellOutputCard />
          </Space>
        </RightPanel>
      </PageContentBase>
    </Page>
  );
}
